import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { UniversityDetailsPage } from './university-details.page';

const routes: Routes = [
  {
    path: '',
    component: UniversityDetailsPage
  },
  {
    path: 'university-gallery/:universityMainID',
    loadChildren: () => import('./university-gallery/university-gallery.module').then( m => m.UniversityGalleryPageModule)
  },
  {
    path: 'university-fees/:universityID',
    loadChildren: () => import('./university-fees/university-fees.module').then( m => m.UniversityFeesPageModule)
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class UniversityDetailsPageRoutingModule {}
